body {
  height:100%;
  position: absolute;
  width:100%;
  background-color: #666e8b;

}

.App {
  text-align: center;
  font-family: Impact;
  height: 100%;
}

.App-body {
  background-color: #919ab4;
  height:100%;
  max-width: 70vw;
  font-size: min(20px, 3vw);
  color: #000;
  /* centers the div */
  margin: 0 auto;
  padding: 0.5rem 2rem;
  border-color: 0.4rem solid #000;
  border-width: 0.4rem;
  border-style: solid;
  border-radius: 1rem;
}

